<template>
  <div class="sold-dispatch">
    <div class="sold-dispatch-pic">
      <div class="sold-dispatch-pic-title title1">9</div>
      <div class="sold-dispatch-pic-content">
        <div class="sold-dispatch-pic-content-box" v-for="item in picData" v-bind:key="item.id">
          <img src="../../public/imgs/plus.png" class="sold-dispatch-pic-content-box-img" />
          <input
            type="file"
            accept="image/*"
            id="file"
            class="photo-input"
            @change="addNewPhoto($event,item.id)"
          />
          <div class="photo-show-pic" :style="item.value"></div>
        </div>
      </div>
      <div class="sold-dispatch-pic-title title1">15s</div>
      <div class="sold-dispatch-pic-content">
        <div class="sold-dispatch-pic-content-box" v-for="item in videoData" v-bind:key="item.id">
          <img src="../../public/imgs/plus.png" class="sold-dispatch-pic-content-box-img" />
          <input
            type="file"
            accept="video/*"
            id="file"
            class="photo-input"
            @change="addNewVideo($event,item.id)"
          />
          <video class="video-show-pic" :src="item.value"></video>
        </div>
      </div>
    </div>
    <div class></div>
    <component v-for="item in formList" v-bind:key="item.id" :is="item.type" :formdata="item"></component>
    <CheckboxParten1 :checkboxdata="checkBoxData"></CheckboxParten1>
    <div class="line"></div>
    <component :is="formList2[0].type" :formdata="formList2[0]"></component>
    <RadioParten1 :radiodata="radioList[0]"></RadioParten1>
    <RadioParten1 :radiodata="radioList[1]"></RadioParten1>
    <div class="bottom-blank"></div>
    <div class="submit-btn" @click="submit"></div>
  </div>
</template>

<script>
import FormInput from '../components/FormInput';
import CheckboxParten1 from '../components/CheckboxParten1';
import RadioParten1 from '../components/RadioParten1';
export default {
    name: 'Page3DispatchSold',
    data: function() {
        return {
            soldData: {
                photoCount: 0,
                videoCount: 0,
            },
            picData: [{ id: 0, value: '' }],
            videoData: [{ id: 0, value: '' }],
            checkBoxData: {
                name: 'houseSpecial',
                title1: '',
                title2: '',
                list: [
                    { id: 0, name: '', checked: false },
                    { id: 1, name: '', checked: false },
                    { id: 2, name: '', checked: false },
                    { id: 3, name: '', checked: false },
                    { id: 4, name: '', checked: false },
                    { id: 5, name: '', checked: false },
                    { id: 6, name: '', checked: false },
                ],
            },
            formList: [
                {
                    id: 0,
                    type: 'FormInput',
                    placeholder: '',
                    title: '',
                },
                {
                    id: 1,
                    type: 'FormInput',
                    placeholder: '',
                    title: '',
                },
                {
                    id: 2,
                    type: 'FormInput',
                    placeholder: '',
                    title: '',
                },
                {
                    id: 3,
                    type: 'FormInput',
                    placeholder: '',
                    title: '',
                },
                {
                    id: 4,
                    type: 'FormInput',
                    placeholder: '',
                    title: '',
                },
                {
                    id: 5,
                    type: 'FormInput',
                    placeholder: '',
                    title: '',
                },
                {
                    id: 6,
                    type: 'FormInput',
                    placeholder: '',
                    title: '',
                },
                {
                    id: 7,
                    type: 'FormInput',
                    placeholder: '/',
                    title: '',
                },
                {
                    id: 8,
                    type: 'FormInput',
                    placeholder: '',
                    title: '',
                },
                {
                    id: 9,
                    type: 'FormInput',
                    placeholder: '',
                    title: '',
                },
                {
                    id: 10,
                    type: 'FormInput',
                    placeholder:
                        '',
                    title: '',
                },
                {
                    id: 11,
                    type: 'FormInput',
                    placeholder: '',
                    title: '',
                },
                {
                    id: 12,
                    type: 'FormInput',
                    placeholder: '',
                    title: '',
                },
                {
                    id: 13,
                    type: 'FormInput',
                    placeholder: '',
                    title: '',
                },
                {
                    id: 14,
                    type: 'FormInput',
                    placeholder: '',
                    title: '',
                },
                {
                    id: 15,
                    type: 'FormInput',
                    placeholder: '',
                    title: '',
                },
                {
                    id: 16,
                    type: 'FormInput',
                    placeholder: '',
                    title: '',
                },
            ],
            formList2: [
                {
                    id: 0,
                    type: 'FormInput',
                    placeholder: '',
                    title: '',
                },
            ],
            radioList: [
                {
                    name: 'agree promise',
                    checked: false,
                    text: [''],
                    fontClass: 'content2',
                    activeClass: 'content3',
                },
                {
                    name: 'deposit',
                    checked: false,
                    text: [
                        '',
                        '',
                        '',
                        '',
                    ],
                    fontClass: 'content3 ',
                    activeClass: 'content3',
                },
            ],
            
        };
    },
    created: function() {
        // this.addNewPhoto();
        // this.addNewVideo();
        this.$bus.on('checkbox click', this.checkboxHouseSpecial);
        this.$bus.on('radio click', this.radioChange);
    },
    components: {
        FormInput,
        CheckboxParten1,
        RadioParten1,
    },
    methods: {
        submit: function() {
                this.$bus.emit('page3 submit', {});
            },
        addNewPhoto: function(e, id) {
            console.log(id);
            console.log(e.target.files[0]);

            var that = this;
            var file = e.target.files[0];
            if (file.type.indexOf('image') == 0) {
                var reader = new FileReader();
                reader.readAsDataURL(file);
                reader.onload = function(e) {
                    // base64
                    var newUrl = this.result;
                    console.log(newUrl);
                    that.picData[that.picData.length - 1].value =
                        'background:url(' + newUrl + ');backgroundSize:cover';
                    var obj = { id: that.picData.length, value: '' };
                    that.picData.push(obj);
                };
            }

            return;
        },
        addNewVideo: function(e, id) {
            console.log(id);
            console.log(e.target.files[0]);

            var that = this;
            var file = e.target.files[0];
            if (file.type.indexOf('video') == 0) {
                var reader = new FileReader();
                reader.readAsDataURL(file);
                reader.onload = function(e) {
                    // base64
                    var newUrl = this.result;
                    console.log(newUrl);
                    that.videoData[that.videoData.length - 1].value = newUrl;
                    var obj = { id: that.videoData.length, value: '' };
                    that.videoData.push(obj);
                };
            }
        },
        checkboxHouseSpecial: function(data) {
            if (data.name == 'houseSpecial') {
                var list = this.checkBoxData.list;
                list.forEach(element => {
                    if (element.id == data.id) {
                        console.log(element);
                        element.checked = !element.checked;
                    }
                });
                this.checkBoxData.list = list;
            }
        },
        radioChange: function(data) {
            console.log(data);

            this.radioList.forEach(item => {
                if (item.name == data.name) {
                    console.log(item.name);
                    item.checked = !item.checked;
                }
            });
            console.log(this.radioList);
            // this.radioData.checked = !this.radioData.checked;
        },
    },
};
</script>
<style scoped>
.submit-btn {
    position: fixed;
    bottom: 0;
}
</style>

<style>
.line {
    width: 100%;
    height: 3vw;
}
</style>


